import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { MasterService } from './../../../masterservices/master.service';
import { FormGroup, FormControl, Validators, FormBuilder, AbstractControl, FormArray,
 } from '@angular/forms';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';


@Component({
  selector: 'app-teacher-detail',
  templateUrl: './teacher-detail.component.html',
  styleUrls: ['./teacher-detail.component.css'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'en-GB'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
})
export class TeacherDetailComponent implements OnInit {
  id: String;
  selected: any = {};
  updateForm: FormGroup;
  users = [];
  classes = [];
  religions = [];
  castes = [];
  subjects = [];
  castesfiltered = [];
  isSave = true;
  disableSave = false;
  // tslint:disable-next-line:max-line-length
  constructor(private masterService: MasterService, private router: Router, private route: ActivatedRoute, private snackBar: MatSnackBar, private fb: FormBuilder) {
    this.createForm();
  }
  createForm() {
    this.updateForm = this.fb.group({
      name: ['', Validators.required],
      PEN: ['', [Validators.required, ]],
      address: ['', Validators.required],
      myClasses: '',
      dob: '',
      dateOfJoin: ['', Validators.required],
      mobileNo: ['', Validators.required],
      myUser: ['', Validators.required],
      mySubjects: ['', Validators.required],
      myRelegion: '',
      myCaste: '',
    });
  }
  // get fData() {
  //   return this.updateForm.get('myClasses') as FormArray;
  // }
  ngOnInit() {
    // return;
    console.log(this.route.params);
    this.masterService.getUsers()
    .subscribe(
      res => this.users = res,
      err => console.log(err)
    );
    this.masterService.getReligions()
    .subscribe(
      (res) => {this.religions = res; },
      err => console.log(err)
    );
    this.masterService.getCastes()
    .subscribe(
      (res) => {this.castes = res;
          this.castesfiltered = res;
      },
      err => console.log(err)
    );
    this.masterService.getClasses()
    .subscribe(
      res => this.classes = res,
      err => console.log(err)
    );
    this.masterService.getSubjects()
    .subscribe(
      res => this.subjects = res,
      err => console.log(err)
    );
    // this.castesfiltered = this.castes;
    // var index = 0;
    this.route.params.subscribe(params => {
      this.id = params.id;
      const control = <FormArray>this.updateForm.controls['myClasses'];
      // console.log('ngOnInit: '+this.id);
      console.log(this.id);
      if (this.id !== '__new')
      {
        this.isSave = false;
        this.masterService.getTeacherById(this.id).subscribe(res => {
          this.selected = res;
          console.log(this.selected);
          // tslint:disable-next-line:forin
          for (const field in this.updateForm.controls) {
            if (field === 'myClasses') {
              this.updateForm.get(field).setValue(this.getSelectData(this.selected[field], this.classes));
            } else if (field === 'mySubjects') {
              this.updateForm.get(field).setValue(this.getSelectData1(this.selected[field], this.subjects));
            } else {
              this.updateForm.get(field).setValue(this.selected[field]);
            }
          }
        });
      }
    });
  }
  getSelectData(dataFromTable, listData) { 
    var array: any[] = [];
    array.length = 0;
    dataFromTable.forEach((value, index) => {
      const itemIndex = listData.find(x => x._id === value);
      array.push(itemIndex);
    });
    return array;
  }
  getSelectData1(dataFromTable, listData) { 
    var array1: any[] = [];
    array1.length = 0;
    dataFromTable.forEach((value, index) => {
      const itemIndex = listData.find(x => x._id === value);
      array1.push(itemIndex);
    });
    return array1;
  }
  changedReligion(code){
    this.castesfiltered = this.castes.filter(f => f.religions === code);
    if(this.castesfiltered.length > 0) {
      this.updateForm.get('myCaste').setValue(this.castesfiltered[0]._id);
    }
  }
  update() {

    if (this.updateForm.invalid) {
      return;
    }
    // tslint:disable-next-line:forin
    for (const field in this.updateForm.controls) {
      this.selected[field] = this.updateForm.get(field).value;
      // console.log(field + ':' + this.selected[field]);
    }
    //  console.log('save : ' + this.selected);
    this.masterService.updateTeacher(this.id, this.selected).subscribe((res) => {
      // tslint:disable-next-line:forin
      // for (const prop in res) {
      //   console.log(prop + ' ' + res[prop]);
      // }
      // console.log(res['message'])
      this.snackBar.open(`Student ${res['message']}`, 'OK', {
        duration: 3000
      });
      if (this.id === '__new' && res['status'] === 'S' ) {
      // this.router.navigate([`/example`]);
        this.disableSave = true;
      }
      this.router.navigate([`/teacherdetail/${res['id']}`]);
    });

  }
}
