// import { element } from 'protractor';
import { MasterService } from './../../../masterservices/master.service';
import { AttendanceService } from './../../../transervice/attendance.service';

import { Component, OnInit } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { MatTableDataSource } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
// import { Observable } from 'rxjs/Observable';
// import 'rxjs/add/observable/of';
import { FormGroup, FormControl, Validators, FormBuilder, AbstractControl,
  FormGroupDirective, NgForm } from '@angular/forms';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
export interface StudentsData {
  teachers: string;
  students: string;
  description: string;
  dates: Date;
  att: string;
  periods: string;
  classNo: number;
  name: string;
  attendance: boolean;
}



@Component({
  selector: 'app-attendance',
  templateUrl: './attendance.component.html',
  styleUrls: ['./attendance.component.css'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'en-GB'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
})
export class AttendanceComponent implements OnInit {
  displayedColumns: string[] = [ 'classNo', 'name', 'attendance'];
  //  dataSource = new MatTableDataSource<StudentsData>(this.ELEMENT_DATA);
  selection = new SelectionModel<StudentsData>(true, []);
  form: FormGroup;
  classes = [];
  subjects = [];
  students = [];
  periods = [];
  userid: string;
  teacher: any = {};
  dataSource1: DataSource<any>;
  tableData = [];
  attendance = [];
  constructor(private masterService: MasterService, private attendacneService: AttendanceService, private fb: FormBuilder) { 
    this.createForm();
  }
  createForm() {
    this.form = this.fb.group({
      myClass: [''],
      period: [''],
      mySubject: [''],
      dates: [''],
    });
  }
  ngOnInit() {
    this.userid = localStorage.getItem('userid');
    const currentDate = new Date();
    this.form.get('dates').setValue (currentDate);
    this.masterService.getTeachersByUser(this.userid)
    .subscribe(
      res => {
        this.teacher = res;
        console.log('Teacher: ' + this.teacher._id);
        this.masterService.getClassesByTeacher(this.teacher._id)
        .subscribe(
          (res1) => {
            this.classes = res1;
            if (this.classes.length > 0) {
              this.form.get('myClass').setValue(this.classes[0]._id);
              this.fillStudentData();
            }
            console.log(res1);
          },
          err => console.log(err)
          );
        this.masterService.getSubjectsByTeacher(this.teacher._id)
        .subscribe(
          (res2) => {
            this.subjects = res2;
            if (this.subjects.length > 0) {
              this.form.get('mySubject').setValue(this.subjects[0]._id); 
            }
            console.log(res2);
          },
          err => console.log(err)
          );
      }
    );
    this.masterService.getPeriod()
    .subscribe(
      (res) => {
        this.periods = res;
        this.form.get('period').setValue(this.periods[0]._id);
      },
      err => console.log(err)
    );
  }
  classChanged(code) {
    this.fillStudentData();
  }
  fillStudentData() {
    this.masterService.getStudentByClass(this.form.get('myClass').value)
    .subscribe(
      (res) => {
        console.log(res);
        this.students = res;
        // console.log(this.students[0]['name']);
        // tslint:disable-next-line:prefer-const
        let len = this.students.length;
        console.log(len);
        let std: StudentsData;
        this.tableData.length = 0;
        for (let i = 0; i < len; i++) {
          std = {
              teachers: this.teacher['_id'],
              students: this.students[i]['_id'],
              description: '',
              dates: this.form.get('dates').value,
              att: '',
              periods: this.form.get('period').value,
              classNo: this.students[i]['classNo'],
              name: this.students[i]['name'],
              attendance: true
          };
          this.tableData.push(std);
        }
        this.dataSource1  = new MatTableDataSource(this.tableData);
        console.log(this.tableData);
      },
      err => console.log(err)
      );
  }
  isAllSelected() {
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {

  }
  update() {
    // tslint:disable-next-line:no-shadowed-variable
    this.tableData.forEach(element => {
      const toDo = element as StudentsData;
      toDo.dates = this.form.get('dates').value;
      toDo.att = toDo.attendance ? 'P' : 'A';
      toDo.periods = this.form.get('period').value;
    });
    // console.log(this.tableData);
    this.attendacneService.updateAttendance('a', this.tableData).subscribe((res) => {
      console.log(res);
    });
  }
  onChangeCheckBox(event, index, item) {
    item.attendance = !item.attendance;
    // console.log(item);
    // console.log(index);
  }
}
